import React from "react";
import { Link } from "gatsby";
import CallToAction from "../master/cta";
import img5 from "../../assets/images/gallery/image 5.png";
import img2 from "../../assets/images/gallery/image2.png";
import icon from "../../assets/images/gallery/icon.svg";

import "./gallery.css";

const Gallery = () => {
  return (
    <div className="home-content">
      <div className="header-title">
        <div className="container">
          <div className="header-title-text">
            <h2>
              Our <span>Gallery</span>
            </h2>
            <ul>
              <li>
                <Link to="/">Home</Link>
              </li>
              <li>/ Gallery</li>
            </ul>
          </div>
        </div>
      </div>
      <div className="gallery-section">
        <div className="container">
          <div className="gallery-grid-section">
            <div className="gallery-box">
              <img src={img5} alt="galleryImg" />
              <div className="gallery-box-link">
                <h4>
                  Product<span>Gallery</span>
                </h4>
                <Link to="/productGallery">
                  Explore More
                  <img src={icon} alt="" />
                </Link>
              </div>
            </div>
            <div className="gallery-box">
              <img src={img2} alt="galleryImg" />
              <div className="gallery-box-link">
                <h4>
                  Project<span>Gallery</span>
                </h4>
                <Link to="/projectGallery">
                  Explore More
                  <img src={icon} alt="" />
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
      <CallToAction />
    </div>
  );
};

export default Gallery;
